<template>
  <div>
    <header class="header">
      <h1>Privacy Policy</h1>
    </header>
    <main class="markdown-content">
      <!-- 使用 v-html 渲染解析后的 Markdown -->
      <div v-html="renderedMarkdown"></div>
    </main>
  </div>
</template>

<script>
import markdownIt from "markdown-it";
// 引入 Markdown 文件
import mdContent from "@/markdown/Privacy.md";

export default {
  name: "PrivacyPolicy",
  data() {
    return {
      renderedMarkdown: "",
    };
  },
  mounted() {
    const md = new markdownIt();
    this.renderedMarkdown = md.render(mdContent);
  },
};
</script>

